<template>
    <div class="evaluateWrap">
        <div class="header">
            <section class="rating_header_left">
                <p>{{shop_scores.overall_score ? shop_scores.overall_score.toFixed(1):null}}</p>
                <p>综合评价</p>
                <p>高于周边商家{{shop_scores.compare_rating ? shop_scores.compare_rating.toFixed(3)*100 : null}}%</p>
            </section>
            <section class="rating_header_right">
                <p>
                    <span>服务态度</span>
                    <el-rate
                            :value="shop_scores.service_score ? shop_scores.service_score.toFixed(1)-0 : null"
                            disabled
                            show-score
                            text-color="#ff9900">
                    </el-rate>
                </p>
                <p>
                    <span>菜品评价</span>
                    <el-rate
                            :value="shop_scores.food_score ? shop_scores.food_score.toFixed(1)-0 : null"
                            disabled
                            show-score
                            text-color="#ff9900"
                            disabled-void-color="#d1d1d1">
                    </el-rate>
                </p>
                <p>
                    <span>送达时间</span>
                    <span class="delivery_time">{{shop_scores.deliver_time}}分钟</span>
                </p>
            </section>
        </div>
        <ul class="tag_list_ul">
            <li class="tag_list_lis" :class="{unsatisfied:isChoose == index ? false : item.unsatisfied,tagActivity:isChoose == index ? true : false}" v-for="(item, index) in rating_tags" :key="item._id" @click="chooseTags(index,item.name)">
                {{item.name}}({{item.count}})
            </li>
        </ul>
        <ul class="rating_list_ul">
            <li v-for="(rat,j) in ratings" :key="rat._id">
                <img :src="rat.avatar.length ?'https://fuss10.elemecdn.com/'+rat.avatar.substr(0,1)+'/'+rat.avatar.substr(1,2)+'/'+rat.avatar.substr(3)+'.jpeg' : 'https://elm.cangdu.org/img/default.jpg'" alt="">
                <section class="rating_list_details">
                    <div>
                        <div class="username_star">
                            <p>{{rat.username}}</p>
                            <p>
                                <el-rate
                                        :value="rat.rating_star"
                                        disabled
                                        text-color="#ff9900">
                                </el-rate>
                                <span>{{rat.time_spent_desc}}{{rat.rating_text}}</span>
                            </p>
                        </div>
                        <div class="rated_at">{{rat.rated_at}}</div>
                    </div>
                    <ul class="food_img_ul">
                        <li v-for="(evaluateimg, x) in rat.item_ratings">
                            <img :src="evaluateimg.image_hash.length ? 'https://fuss10.elemecdn.com/'+evaluateimg.image_hash.substr(0,1)+'/'+evaluateimg.image_hash.substr(1,2)+'/'+evaluateimg.image_hash.substr(3)+'.jpeg' : null" alt="">
                        </li>
                    </ul>
                    <ul class="food_name_ul">
                        <li class="ellipsis" v-for="(evaluatename, y) in rat.item_ratings" :key="evaluatename._id">{{evaluatename.food_name}}</li>
                    </ul>
                </section>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "Evaluate",
        data(){
            return {
                shopId:null,
                shop_scores:{},
                rating_tags:[],
                isChoose:0,
                ratings:[]
            }
        },
        created(){
            this.$parent.is_border = false;
            this.shopId = JSON.parse(localStorage.getItem("current_shop")).id;
            //获取商店评价分数
            this.axios.get("/ugc/v2/restaurants/"+this.shopId+"/ratings/scores").then(res=>{
                this.shop_scores = res;
            }).catch(err=>{});
            //获取商店评价分类
            this.axios.get("/ugc/v2/restaurants/"+this.shopId+"/ratings/tags").then(res=>{
                this.rating_tags = res;
            }).catch(err=>{});
            ////获取全部评价
            this.axios.get("/ugc/v2/restaurants/"+this.shopId+"/ratings").then(res=>{
                this.ratings = res;
            }).catch(err=>{});
        },
        methods:{
            chooseTags(i,name){
                this.isChoose = i;
                this.axios.get("/ugc/v2/restaurants/"+this.shopId+"/ratings",{
                    params:{
                        tag_name:name
                    }
                }).then(res=>{
                    cthis.ratings = res;
                }).catch(err=>{});
            }
        }
    }
</script>

<style scoped>
    .header {
        display: flex;
        background-color: #fff;
        padding: .8rem .5rem;
        margin-bottom: .5rem;
    }
    .rating_header_left {
        flex: 3;
        text-align: center;
    }
    .rating_header_left>p:nth-child(1) {
        font-size: 1.2rem;
        color: #f60;
    }
    .rating_header_left>p:nth-child(2) {
        font-size: .65rem;
        color: #666;
        margin: .25rem 0;
    }
    .rating_header_left>p:nth-child(3) {
        font-size: .4rem;
        color: #999;
    }
    .rating_header_right {
        flex: 4;
    }
    .rating_header_right>p {
        font-size: .65rem;
        line-height: 1rem;
        display: flex;
        align-items: center;
        justify-content: flex-start;
    }
    .rating_header_right>p>span:first-of-type {
        color: #666;
        margin-right: .5rem;
    }
    .delivery_time {
        font-size: .4rem;
        color: #999;
    }
    .tag_list_ul {
        display: flex;
        flex-wrap: wrap;
        background-color: #fff;
        padding: .5rem;
    }
    .tag_list_lis {
        font-size: .6rem;
        color: #6d7885;
        padding: .3rem;
        background-color: #ebf5ff;
        border-radius: .2rem;
        border: 1px;
        margin: 0 .4rem .2rem 0;
    }
    .tagActivity {
        background-color: #3190e8;
        color: #fff;
    }
    .unsatisfied {
        background-color: #f5f5f5;
        color: #aaa;
    }
    .rating_list_ul {
        background-color: #fff;
        padding: 0 .5rem;
    }
    .rating_list_ul>li {
        border-top: 1px solid #f1f1f1;
        display: flex;
        padding: .6rem 0;
    }
    .rating_list_ul>li>img {
        width: 1.5rem;
        height: 1.5rem;
        border: .025rem;
        border-radius: 50%;
        margin-right: .8rem;
    }
    .rating_list_details {
        flex: 1;
    }
    .rating_list_details>div {
        display: flex;
        flex: 1;
        justify-content: space-between;
        margin-bottom: .3rem;
    }
    .username_star {
        font-size: .55rem;
