    .username_star>p:last-child {
        display: flex;
        align-items: center;
    }
    .username_star>p:last-child>span {
        font-size: .55rem;
        color: #666;
        margin-left: .15rem;
    }
    .rated_at {
        font-size: .4rem;
        color: #999;
    }
    .food_img_ul {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: .4rem;
    }
    .food_img_ul>li>img {
        width: 3rem;
        height: 3rem;
        margin-right: .4rem;
        display: block;
    }
    .food_name_ul {
        display: flex;
        flex-wrap: wrap;
    }
    .food_name_ul>li {
        font-size: .55rem;
        color: #999;
        width: 2.2rem;
        padding: .2rem;
        border: .025rem solid #ebebeb;
        border-radius: .15rem;
        margin-right: .3rem;
        margin-bottom: 4px;
    }
</style>
<style>
    .el-rate i{
        font-size: .5rem;
        margin-right: -.1rem;
    }
    .el-rate__text {
        margin-left: .5rem;
        font-size: .5rem;
    }
    .username_star>p:last-child>div {
        width: 2rem;
        display: flex;
        align-items: center;
        margin-right: .1rem;
    }
</style>